@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;500&display=swap');

.container.one{
    width: 100%;
    color: #06FF00;
    /* background-color: #FAF4B7; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  
  
    
}
.container.one .content{
    width: 60%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;    
    font-weight: 600;
    /* -webkit-text-stroke: 1px #FFAC41;  */
}

.container.one .content .head{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    height: auto;
}


.container.one .content .head .text{
    /* width: 140px; */
    height: 120px;
    white-space: nowrap;
    position: relative;
    /* font-family: 'Staatliches', cursive; */
    font-family: 'Source Code Pro', monospace;
    font-size: 80pt; 
    justify-content: flex-start;
    overflow: hidden;
    /* border-right: 2px solid black; */
    

}




.container.one .content .writeup{
    width: 100%;
    position: relative;
    display: flex;
    font-family: 'Source Code Pro', monospace;
    font-size: 25pt; 
    justify-content: flex-start;
    margin-top: 40px;
}

.container.one .image{
    width: 40%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* position: relative; */
    /* background-color: aliceblue; */
}

.container.one .image .day{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.container.one .image .day img{
    width: 100%;
}



.container.one .content .stack{
    /* visibility: hidden; */
    display: flex;
    justify-content: flex-start;
    width: 100%;
    flex-direction: column;
    font-family: 'Source Code Pro', monospace;
    font-size: 17pt;
    /* padding-top: 100px; */
    height: 0px;
    overflow: hidden;
}

.container.one .content .stack .line{
    /* padding: 10px 10px 10px 5px; */
    padding: 20px 20px 20px 0px;
}

